import React from "react"
import { graphql } from "gatsby";
import BlogListPage from "../../components/BlogListPage";
import Layout from "../../components/layout";

export default class BlogList extends React.Component {
  render() {
    const posts = this.props.data.allMarkdownRemark;
    const adsData = [{
      id: "426407765", size: "970x250", id2: "426407765", isMobile: false
    }, {
      id: "188970836", size: "300x250", id2: "188970836", isMobile: true
    }, {
      id: "617163306", size: "320x50", id2: "617163306", isMobile: true 
    }];

    return (
      <Layout adsData={adsData}>
        <BlogListPage
          title={`${this.props.pageContext.tag}`}
          data={posts}
          tag={`blogs/${this.props.pageContext.tag}`}
        />
      </Layout>
    );
  }
}

export const blogListQuery = graphql`
  query taggedBlogListQuery($skip: Int!, $limit: Int!, $tag: String) {
    allMarkdownRemark(
      sort: {
          order: DESC, fields: [frontmatter___date],
      }
      filter: {
        frontmatter:{tags:{in: [$tag]}}
      }
      limit: $limit
      skip: $skip
    ) {
      pageInfo {
        hasNextPage
        hasPreviousPage
        currentPage
      }
      edges {
        node {
          id
          excerpt
          frontmatter {
            slug
            title
            date(formatString: "MMMM DD, YYYY")
            cover_photo
          }
        }
      }
    }
  }
`